import * as apiHome from '@/api/home.js'

export default {
	components: {
		'uni-search': UniSearch,
		'uni-load-more': UniLoadMore
	},
	data() {
		return {
			searchBar: {
				placeholder: '搜索感兴趣的文章',
				radius: 25
			},
			selectedTabIndex: 0,
			tabs: [
				{
					tabsHeader: {
						text: '最新',
						value: 'lastest',
						actived: true
					},
					tabsContent: []
				},{
					tabsHeader: {
						text: '热门',
						value: 'hotest',
						actived: true
					},
					tabsContent: [
						{
							id: '1',
							title: '双十一，我们在忙些什么',
							illustrationUrl: articleImage,
							updateAt: '2019-11-11',
							likeNum: 4,
							browserNum: 20,
							commentNum: 2
						}
					]
				},{
					tabsHeader: {
						text: '标签',
						value: 'classify',
						actived: true
					},
					tabsContent: [
						{
							id: '1',
